import React, { Component } from 'react';
import theme, { pxToVh } from '../utils/theme';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Footer from '../components/Footer';
import { Typography, Card, CardContent, Divider, Fab, Paper, Modal, Dialog, DialogContent } from '@material-ui/core';
import { Redirect } from 'react-router-dom';
import { InputBase, TextField, OutlinedInput, Button } from '@material-ui/core';
import {Link} from 'react-router-dom';
import bg3 from '../assets/bg3.jpg'


const styles = (theme) => ({
  mainBackground: {
    backgroundImage: `url(${bg3})`,
    //height: '100vh',
    //width: '100vw',
    //overflow: 'scroll',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
  },
  root: {
    flexGrow: 1,
    paddingLeft: '1vw',
    paddingRight: '1vw',
    minHeight:'100%',
    backgroundImage: `url(${bg3})`,
    //height: '100vh',
    //width: '100vw',
    //overflow: 'scroll',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
  },
  SicMundus: {
    fontFamily: '"Edwardian Script ITC", Times, serif',
    fontSize:'150%',
    fontWeight: 'bold',
    color: '#ffb7bc'
  },
  friendsText: {
    fontFamily: '"Segoe Print", Times, serif',
    color:'#ffb7bc'
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width:'70%'
  },
  cssLabel: {
    color : '#5e7afd',
    '&$cssFocused': {
      color: `#ff6653 !important`,
    }

  },

  cssOutlinedInput: {
    '&$cssFocused $notchedOutline': {
      borderColor: `#5e7afd !important`,
    }
  },

  cssFocused: {
  },

  notchedOutline: {
    borderWidth: '1px',
    borderColor: '#ff6653 !important'
  },
  card: {
    backgroundColor:'transparent',
    border: '2px double #ff6653',
    borderRadius: '10%'
  },
  input: {
    color: '#ffb7bc'
  },
  paper: {
    position: 'absolute',
    width: theme.spacing.unit * 50,
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing.unit * 4,
    outline: 'none',
  },
  searchBtn: {
    marginTop: '8vh',
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: '#5DAAE0',
    '&:hover': {
      backgroundColor: '#5daae0',
      color: 'white',
    },
  },
  searchBtnDisabled: {
    minWidth: '5vw',
    minHeight: '2.188vw',
    fontSize: '0.95vw',
    border: 'solid 0.75px #3B617C',
    // marginRight: '0.5rem',
    alignSelf: 'center',
    color: 'white !important',
    background: '#FFFFFFa5',
    '&:hover': {
      cursor: 'default',
      backgroundColor: '#FFFFFFa5',
    },
  },
});

class LogIN extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: '',
      password: '',
      redirect: false,
      dialog: false,
      /*response: 0,
      
      loading: false,*/
    };
    this.handleNameChange = this.handleNameChange.bind(this);
    this.handlePasswordChange = this.handlePasswordChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }

  handleNameChange= (e) => {
    this.setState({
      userName: e.target.value,
    });
  };
  handlePasswordChange = (event) => {
    this.setState({
      password: event.target.value,
    })
  };
  handleClick = () => {
    if(this.state.userName === 'anuska123' && this.state.password === 'SicMundus'){
      this.setState({
        redirect: true
      })
    }
    else{
      this.setState({
        dialog: true
      })
    }
  };
  handleClose=() =>{
    this.setState({
      dialog: false
    })
  }

  

  componentDidMount() {}

  render() {
    const { classes } = this.props;
    if(this.state.redirect){
      return <Redirect push to='/welcomeAnuska/'/>;
    }
    return (
      //<div className={classes.mainBackground}>
      <Grid container className={classes.root} spacing={2} xs={12} justify='flex-end' alignItems='center'>
        <Grid item xs={8}/>
        <Grid item xs={4} >
          <Card className={classes.card}>
            <CardContent>
              <div style={{width:'100%',textAlign:'center'}}>
              <Typography className={classes.SicMundus}>
                Sic Mundus Creatus Est
              </Typography>
              </div>
              <Divider style={{backgroundColor: '#5e7afd'}}/>
              <div style={{width:'100%',textAlign:'center',marginTop:'2%'}}>
                <Typography variant='body1' className={classes.friendsText}>
                  Joey doesn't share food, nor do we. So,
                </Typography>
                <Typography className={classes.friendsText}>PIVOT!</Typography>
              </div>
              <div style={{width:'100%',marginTop:'2%',textAlign:'center'}}>
              <TextField
                required
                id="outlined-required"
                label="Username"
                //defaultValue="Hello World"
                className={classes.textField}
                margin="normal"
                variant="outlined"
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                    focused: classes.cssFocused,
                    //input: classes.input
                  },
                }}
                InputProps={{
                  classes: {
                    root: classes.cssOutlinedInput,
                    focused: classes.cssFocused,
                    notchedOutline: classes.notchedOutline,
                    input: classes.input
                  },
                }}
                onChange={this.handleNameChange}
              />
              <TextField
                required
                id="outlined-required-password"
                label="Password"
                //defaultValue="Hello World"
                className={classes.textField}
                margin="normal"
                variant="outlined"
                type="password"
                InputLabelProps={{
                  classes: {
                    root: classes.cssLabel,
                    focused: classes.cssFocused,
                  },
                }}
                InputProps={{
                  classes: {
                    root: classes.cssOutlinedInput,
                    focused: classes.cssFocused,
                    notchedOutline: classes.notchedOutline,
                    input: classes.input
                  },
                }}
                onChange={this.handlePasswordChange}
              />
              </div>
              <div style={{width:'100%',textAlign:'center'}}>
                <Fab
                 variant='extended'
                 style={{width:'30%',backgroundColor:'#5e7afd',marginTop:'1vh',border:'0.8px solid #ffb7bc'}}
                 //component={Link}
                 //to="/welcomeAnuska/"
                 onClick={this.handleClick}
                 >
                  GET IN!
                </Fab>
                <Dialog
                  open = {this.state.dialog}
                  onClose = {this.handleClose}
                  aria-labelledby="form-dialog-title"
                >
                 <DialogContent style={{backgroundColor:'#ffb7bc',textAlign:'center'}}>
                    <Typography variant='display2'>
                      For God's sake ENTER THE CORRECT CREDENTIALS!
                    </Typography>
                  </DialogContent>
                </Dialog>
              </div>
              <div style={{width:'100%',textAlign:'center',marginTop:'2%'}}>
                <Typography variant='caption' style={{color:'#ffb7bc'}}>
                  Not Anuska? GET OUT!
                </Typography>
              </div>
              
            </CardContent>
          </Card>
        </Grid>
      </Grid>
      //</div>
    );
  }
}

export default withStyles(styles, { withTheme: true })(LogIN);

