import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';

const styles = theme => ({
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
    overflow: 'hidden',
    backgroundColor: 'inherit',
  },
  gridList: {
    width: 500,
    height: 450,
  },
});


 const tileData = [
    {
      img: 'https://www.freshedits.com/star-dust/awkward-actors-who-refused-to-kiss-their-costars-on-camera/img/ouch01_MobileImageSizeReigNN.jpg',
      title: 'C&B hand on shoulder TCCoBB',
      cols: 1,
    },
    {
      img: 'https://i.pinimg.com/originals/50/34/a3/5034a3a7ee9814fa5b90810185c87b93.jpg',
      title: 'C&B hand romantic TCCoBB',
      cols: 2,
    },
    {
      img: 'https://assets.mubicdn.net/images/notebook/post_images_danny/multiple%20images/Magnificent%20Obsession/benjamin.jpg',
      title: 'C&B hand almost intimate TCCoBB',
      cols: 2,
    },
    {
        img: 'https://66.media.tumblr.com/eb54ddb468b1ad4aab3bea6b5bfac038/tumblr_pathlctxLU1wnzksyo4_400.jpg',
        title:'CB Suit',
        cols: 1,
    },
    {
        img: 'https://data.whicdn.com/images/333281205/original.jpg',
        title:'BP smug',
        cols: 1,
    },
    {
        img: 'https://media.vanityfair.com/photos/5ab40ed5affa80574bdbb247/master/pass/Cate-Blanchett.jpg',
        title:'CB smug',
        cols: 1,
    },
    {
        img:'https://i.pinimg.com/originals/2a/80/7f/2a807ff8978664bb1c4ff1a937303543.jpg',
        title: 'BP Ocean\'s 11',
        cols: 1,
    },
    {
        img: 'https://i1.wp.com/metro.co.uk/wp-content/uploads/2009/02/benjaminbutton_450x250.jpg?quality=90&strip=all&zoom=1&resize=450%2C250&ssl=1',
        title: 'C&B Intimate',
        cols: 3,
    },
    {
        img: 'https://i.insider.com/5d4ad73836e03c2e065ade25?width=600&format=jpeg&auto=webp',
        title: 'BP oldie',
        cols: 1,
    },
    {
        img:'https://i.pinimg.com/originals/7f/ff/93/7fff9382008df7d147cfc53168be6615.jpg',
        title: 'CB Hela',
        cols: 1,
    },
    {
        img: 'https://www.nycjackets.com/wp-content/uploads/2019/11/Ad-Astra-Roy-McBride-Brad-Pitt-Jacket.jpg',
        title: 'BP Ad Astra',
        cols: 1,
    } 
  ];
 
function ImageGridList(props) {
  const { classes } = props;

  return (
    <div className={classes.root}>
      <GridList cellHeight={160} className={classes.gridList} cols={3}>
        {tileData.map(tile => (
          <GridListTile key={tile.img} cols={tile.cols || 1}>
            <img src={tile.img} alt={tile.title} />
          </GridListTile>
        ))}
      </GridList>
    </div>
  );
}

ImageGridList.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ImageGridList);
